import { createRouter, createWebHistory } from "vue-router";
import Main from "@/features/Main.vue";
import ErrorPage from "@/features/ErrorPage.vue";

const routes = [
  {
    path: "/",
    component: Main,
    meta: { title: "–ì–ª–∞–≤–Ω–∞—è | dvegasa" },
  },
  {
    path: "/hackathons",
    component: ErrorPage,
    meta: { title: "~errorLabel", errorLabel: "WIP" },
  },
  {
    path: "/blog",
    component: ErrorPage,
    meta: { title: "~errorLabel", errorLabel: "WIP" },
  },
  {
    path: "/portfolio",
    component: ErrorPage,
    meta: { title: "~errorLabel", errorLabel: "WIP" },
  },
  {
    path: "/:pathMatch(.*)*",
    component: ErrorPage,
    meta: { title: "~errorLabel", errorLabel: "404" },
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
